/*------------------------------
	Chosen.js
------------------------------*/

.chosen-container {
	font-size: 1em;
	&.chosen-container-active .chosen-single {
		border-color: $color-primary;
	}
	&.chosen-container-single {
		position: relative;
		.chosen-single {
			display: block;
			div b {
				display: none;
			}
		}
		.chosen-search {
			padding: 5px 10px;
			input[type="text"] {
				border-color: $border-color-light;
			}
		}
		&::after {
			font-family: FontAwesome;
			content: "\F107";
			position: absolute;
			right: 14px;
			top: 14px;
			line-height: 1;
		}
		&:hover {
			cursor: pointer;
			border-color: $border-color;
		}
		&.chosen-rtl::after {
			right: auto;
			left: 14px;
		}
	}
	.chosen-drop {
		border-color: $color-primary;
		border-radius: 0;
	}
	.chosen-results {
		margin: 5px 0;
		padding: 0 10px !important;
		font-size: 0.9em;
		li {
			padding: 0 5px;
			line-height: 2.2;
			&.highlighted {
				color: #000;
				background: none;
			}
			&.group-result {
				color: darken( $color-text, 10% ); 
				text-transform: uppercase;
				background-color: $background-light;
			}
			&.group-option {
				padding-left: 0;
				&:before {
					content: "- ";
				}
				&.level2 {
					margin-left: 1em;
				}
				&.level3 {
					margin-left: 2em;
				}
				&.level4 {
					margin-left: 3em;
				}
			}
		}
	}
}